.app-wrapper {
  margin: 0 auto;
  display: grid;
  width: 1200px;
  grid-template-areas:
  "h h"
  "n c";

  grid-template-rows: 60px 1fr; /*settings rows*/
  grid-template-columns: 2fr 10fr/*settings columns*/
}

.app-wrapper-content {
  grid-area: c;
  background-color: #FFA200;
  color: blue;
}

